.minesweeper {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
}

.game-scroll-container {
    flex: 1;
    overflow: auto;
    position: relative;
}

.game-content {
    padding: 20px;
    min-width: fit-content;
    min-height: fit-content;
}

.game-over-overlay {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    animation: fadeIn 0.3s ease-in;
}

.game-over-overlay h2 {
    font-size: 32px;
    margin-bottom: 16px;
}

.countdown {
    font-size: 18px;
    color: #ccc;
}

@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

.board-container {
    position: relative;
    display: inline-block; /* To wrap content tightly */
} 